import React,{Component} from "react";
import {Link} from "react-router-dom";
import { BASE_URL } from "../baseUrl";
import UserIcon from "./UserIcon";

import "../../css/header.css"



class Header extends Component {

    //                                
    
    render(){

        return(
            <header className="header-container">    
                <h1 className="header-title"> <span></span></h1>
                
                <div className="header-category-container">
                    <div className="header-category-title">
                        <Link to={BASE_URL+"movies"}> <i className="fa header-drop-arrow fa-angle-down" aria-hidden="true"></i></Link>
                    </div>
                    <ul className="header-category-dropdown">
                        <li className="header-link-container">
                            <Link to= { BASE_URL+"movies/action"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"movies/imaginary"}> </Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"movies/family"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"movies/drama"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"movies/animation"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"movies/documentary"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"movies/comedy"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"movies/horrific"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"movies/historical"}></Link>
                        </li>
                    </ul>
               </div>

               <div className="header-category-container">
                    <div className="header-category-title">
                        <Link to={BASE_URL+"series"}>
                             <i className="fa  header-drop-arrow fa-angle-down" aria-hidden="true"></i>
                        </Link>    
                    </div>
                    <ul className="header-category-dropdown">
                        <li className="header-link-container">
                            <Link to={BASE_URL+"series/action"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"series/imaginary"}> </Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"series/family"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"series/drama"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"series/animation"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"series/documentary"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"series/comedy"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"series/horrific"}></Link>
                        </li>
                        <li className="header-link-container">
                            <Link to= {BASE_URL+"series/historical"}></Link>
                        </li>
                    </ul>
               </div>

               <div className="header-search-container">
                    <input className="header-search-input" placeholder=""/>
                    <button className="header-search-btn"><i className="fa fa-search" aria-hidden="true"></i></button>
               </div>

               <div className="header-user-options">
                    {
                        window.location.pathname == "/" ?
                        <></>:                   
                        <div className="header-returnHome">
                            <Link to={BASE_URL}><i className="fa fa-home" aria-hidden="true"></i>  </Link>
                        </div>
                    }
                    <UserIcon/>
               </div>

            </header>
        )
    }
}

export default Header;